@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Oxygen", sans-serif;
  background: #324759;
}

@media only screen and (min-width: 550px) and (max-width: 730px) {
  .container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 540px) {
  .container {
    grid-template-columns: repeat(1, 1fr);
  }

  .title h1 {
    font-size: 25px;
  }
}

.card-wrapper{
position: relative;
background: #D9B88F;
width: 100%;
height: auto;
padding: 30px 20px;
border-radius: 5px;

}



  .content{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 1;
  }

  .card-wrapper::after{
    content: '';
    position: absolute;
     background: #D99F59;
      border-radius: 5px;
      width: 100%;
      height: 146px;
      top: 104px;
      left: 0px;
      z-index: 0;
    }

  .content>p{
    padding: 5px 0;
color: #324759;
font-weight: 700;
  }

  
  .profile{
    width:100px;
  height:100px;
  border:5px solid #F27C38;
  border-radius:100%;
    z-index: 1;
    overflow: hidden;
  }

  .profile img{
    width: 100%;
    left: 0px;
    position: relative;
  }

   


.wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}



.wrapper > .title{
    padding: 50px 0; 
}

.title{
  color: #F27C38;
}

.container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 25px

}
